a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

h1 {
  font-size: 30px;
  min-width: 100%;
  font-weight: bold;
  margin: 0;
}

span {
  font-size: 14px;
}

button {
  border-radius: 20px;
  border: 1px solid #318930;
  background-color: #318930;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: -webkit-transform 80ms ease-in;
  transition: transform 80ms ease-in;
  transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}
button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}
button:hover {
  cursor: pointer;
}

