/*For the input with dynamic label*/

/*Colors*/

.floating-input {
  position: relative;
  /* padding-top: 1rem; */
  height: 40px;
  width: 100%;
  /* background: rgba(57, 63, 84, 0.8); */
  background: rgb(199, 198, 198);
}

.floating-input::after {
  content: "";
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 999;
  height: 2px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  background-position: 0% 0%;
  background: linear-gradient(
    to right,
    #b294ff,
    #57e6e6,
    #feffb8,
    #57e6e6,
    #b294ff,
    #57e6e6
  );
  background-size: auto;
  background-size: 500% auto;
  -webkit-animation: gradient 3s linear infinite;
  animation: gradient 3s linear infinite;
}

.floating-input label {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#jsession {
  position: relative;
  top: 4px;
  border-style: none;
  outline: none;
  font-size: 14px;
  width: 200px;
  height: 25px;
