.floating-label {
  position: absolute;
  pointer-events: none;
  font-size: 16px;
  left: 33%;
  top: 10px;
  transition: 0.2s ease all;
}

input:focus ~ .floating-label,
input:not(:focus):valid ~ .floating-label {
  top: 0px;
  font-size: 12px;
  left: 37%;
  /* top: 1px; */
  color: rgb(65, 65, 65);
}

